import {useEffect} from 'react';
import { Link, Route, Switch, useRouteMatch } from 'react-router-dom';
import { useHistory } from 'react-router-dom';
import {useDispatch, useSelector} from 'react-redux';
import {LoadUser} from '../../application/middlewares/auth';
import {getToken} from '../../application/selectors/auth';
import Input from "@material-tailwind/react/Input";
import Button from "@material-tailwind/react/Button";
import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';
import { useForm, Controller} from "react-hook-form";
import { post } from '../../application/middlewares';
import { 
    setToken,loginSuccess
} from "../../application/actions/auth";
import toast from 'react-hot-toast';
import { GetCookie, SetCookie, RemoveCookie } from '../../utils/cookie'
import logo1 from '../image/logo.svg'




function ChangePassword(){
    const {  control, handleSubmit, formState: { errors }, setError} = useForm({
        resolver: yupResolver(schema)
    });
    const dispatch = useDispatch();
    const token = useSelector(getToken);
    // console.log(token)
    // const isLogged = useSelector(getIsLogged);
    const history = useHistory();
    // console.log(token);
    // console.log(data);
    // useEffect(() => {
    //     if(isLogged === false){
    //         dispatch(LoadUser({history}));
    //     }
    //     // eslint-disable-next-line
    // }, [dispatch, isLogged]);


    const onSubmit = (data) => {
    dispatch(post({
        url:'user/change-password',
        token: `${token}`,
        data,
        action:(response)=>{
            if(response.success){
                console.log("gelen change",response.data)
                // dispatch(setToken(response.data.token));
                dispatch(loginSuccess({"token":response.data.token}));
                SetCookie('refresh_token', response.data.refresh_token);
                history.push('/auth/login');
                toast.success("Parolyňyz stünlikli üýtgedildi!");
            }else {
                console.log("gelen change error",response);
                setError('code', { type:'manual', message:response?.message?.data?.error?.code});

            }
        },
    }))
}
    return(
        <div className="flex justify-center items-center h-full w-full">
        <div className="flex max-w-sm mx-auto overflow-hidden bg-white rounded-lg shadow-lg dark:bg-gray-800 lg:w-80">
        <form onSubmit={handleSubmit(onSubmit)} className="w-full px-6 py-8 md:px-8">
        <div className='flex justify-center w-full'>
            <img src={logo1} className='auth_img' />
            </div>
            <h2 className="text-2xl font-semibold text-center text-gray-700 dark:text-white">Change password</h2>
            {/* <p className="text-xl text-center text-gray-600 dark:text-gray-200">Welcome back!</p> */}
            <div className="mt-4 w-full">
                <Controller
                    control={control}
                    name="code"
                    render={({ field: { onChange, onBlur, value, ref } }) => {
                        return (
                            <Input
                            onChange={onChange} onBlur={onBlur} 
                           type="code"  size="regular" color="blue"
                           outline={true} placeholder="Code" error={errors.code?.message} />
                    )}}
                />
            </div>
            <div className="mt-6 w-full">
            <Controller
                    control={control}
                    name="password"
                    render={({ field: { onChange, onBlur, value, ref } }) => {
                        return (
                            <Input
                            onChange={onChange} onBlur={onBlur} 
                           type="password"  size="regular" color="blue"
                           outline={true} placeholder="Password" error={errors.password?.message} />
                    )}}
                />
            </div>
            
           

            <div className="mt-8 w-full flex justify-end items-center">
            <Button
                color="lightBlue"
                buttonType="link"
                size="regular"
                rounded={false}
                block={false}
                iconOnly={false}
                ripple="dark"
            >
                send
            </Button>
            </div>
        </form>
    </div>
    </div>
    )
}

const schema =  Yup.object().shape({
    password: Yup.string().min(6, "Azyndan 6 simwol").max(50, "Iň köp 50 simwol").required('Iň azyndan 8 simwol bolmaly, iň bolmanda 1 harp we 1 san bolmaly').matches(
        /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/,
      "'Iň bolmanda 1 harp we 1 san bolmaly'"
    ),
  });
export default ChangePassword;